/**
 * CaseSelectorDialog -      
 *
 *  CODE-003: Migrated to TypeScript
 *
 *  dialog   :
 * 1.    ( )
 * 2.   
 *
 *  DARK THEME - Updated to match Dashboard styling
 */

import React, { useState, useEffect, useMemo, type FC, type ChangeEvent } from 'react';
import {
  Search,
  Plus,
  FileText,
  X,
  Loader2,
  Folder,
  Calendar,
  MessageCircle
} from 'lucide-react';
//  PERF FIX: Use centralized store to prevent duplicate API calls
import { useCasesStore } from '../store/useCasesStore';

// ===========================
// Types
// ===========================

type CaseStatus = 'completed' | 'analyzing' | 'pending' | string;

interface CaseItem {
  id: string;
  name?: string;
  case_name?: string;
  case_number?: string;
  created_at?: string;
  status?: CaseStatus;
}

interface CaseSelectorDialogProps {
  onClose: () => void;
  onSelectCase?: (caseItem: CaseItem) => void;
  onNewCase?: () => void;
  onUploadToCase?: (caseItem: CaseItem) => void;
}

// ===========================
// Component
// ===========================

const CaseSelectorDialog: FC<CaseSelectorDialogProps> = ({
  onClose,
  onSelectCase,
  onNewCase,
  onUploadToCase
}) => {
  const [searchQuery, setSearchQuery] = useState('');

  //  Use centralized store
  const { cases, casesLoading: loading, fetchCases } = useCasesStore();

  // Fetch cases on mount (uses caching)
  useEffect(() => {
    fetchCases();
  }, [fetchCases]);

  // Filter cases by search (memoized)
  const filteredCases = useMemo(() => {
    if (!searchQuery.trim()) {
      return cases;
    }

    const query = searchQuery.toLowerCase();
    return cases.filter(
      (c: CaseItem) =>
        c.name?.toLowerCase().includes(query) ||
        c.case_number?.toLowerCase().includes(query) ||
        c.id?.toLowerCase().includes(query)
    );
  }, [searchQuery, cases]);

  const handleSelectCase = (caseItem: CaseItem): void => {
    if (onSelectCase) {
      onSelectCase(caseItem);
    }
  };

  const handleNewCase = (): void => {
    if (onNewCase) {
      onNewCase();
    }
  };

  const handleSearchChange = (e: ChangeEvent<HTMLInputElement>): void => {
    setSearchQuery(e.target.value);
  };

  return (
    <div className="fixed inset-0 bg-black/70 backdrop-blur-sm z-50 flex items-center justify-center p-4 animate-fade-in">
      <div className="bg-slate-900/95 backdrop-blur-xl rounded-2xl shadow-2xl shadow-glass border border-white/10 w-full max-w-lg md:max-w-3xl max-h-[85vh] overflow-hidden flex flex-col animate-scale-in">

        {/* Header -  DARK THEME */}
        <div className="bg-gradient-to-r from-purple-600 to-blue-600 p-4 md:p-6 text-white">
          <div className="flex items-center justify-between">
            <div>
              <h2 className="text-xl md:text-2xl font-bold"> </h2>
              <p className="text-purple-100 text-xs md:text-sm mt-1">
                      
              </p>
            </div>
            <button
              onClick={onClose}
              className="p-2 hover:bg-white/20 rounded-lg transition-colors"
            >
              <X size={24} />
            </button>
          </div>
        </div>

        {/* Content -  DARK THEME */}
        <div className="flex-1 overflow-y-auto p-4 md:p-6">

          {/*  1:    */}
          <div className="mb-8">
            <div className="flex items-center gap-3 mb-4">
              <div className="w-10 h-10 bg-blue-500/20 rounded-full flex items-center justify-center border border-blue-500/30">
                <Folder className="text-blue-400" size={20} />
              </div>
              <div>
                <h3 className="text-lg font-bold text-white">  </h3>
                <p className="text-sm text-slate-400">    </p>
              </div>
            </div>

            {/*  -  DARK THEME */}
            <div className="relative mb-4">
              <Search className="absolute right-3 top-1/2 -translate-y-1/2 text-slate-400" size={20} />
              <input
                type="text"
                value={searchQuery}
                onChange={handleSearchChange}
                placeholder="   ,    ..."
                className="w-full pr-10 pl-4 py-3 bg-white/5 border border-white/10 rounded-xl focus:ring-2 focus:ring-purple-500/50 focus:border-purple-500/50 text-right text-white placeholder-slate-500"
              />
            </div>

            {/*   -  DARK THEME */}
            {loading ? (
              <div className="flex items-center justify-center py-8">
                <Loader2 className="animate-spin text-purple-400" size={32} />
              </div>
            ) : filteredCases.length > 0 ? (
              <div className="space-y-2 max-h-[300px] overflow-y-auto pr-1">
                {filteredCases.map((caseItem: CaseItem) => (
                  <div
                    key={caseItem.id}
                    className="w-full text-right p-4 bg-white/5 border border-white/10 rounded-xl hover:border-purple-500/50 hover:bg-white/10 transition-all"
                  >
                    <div className="flex items-center justify-between">
                      <div className="flex-1">
                        <div className="font-medium text-white">
                          {caseItem.case_name || caseItem.name || ' '}
                        </div>
                        <div className="flex items-center gap-4 mt-1 text-sm text-slate-400">
                          {caseItem.case_number && (
                            <span>: {caseItem.case_number}</span>
                          )}
                          {caseItem.created_at && (
                            <span className="flex items-center gap-1">
                              <Calendar size={14} />
                              {new Date(caseItem.created_at).toLocaleDateString('he-IL')}
                            </span>
                          )}
                          {caseItem.status && (
                            <span className={`px-2 py-0.5 rounded-full text-xs border ${
                              caseItem.status === 'completed'
                                ? 'bg-green-500/20 text-green-400 border-green-500/30'
                                : caseItem.status === 'analyzing'
                                ? 'bg-blue-500/20 text-blue-400 border-blue-500/30'
                                : 'bg-slate-500/20 text-slate-400 border-slate-500/30'
                            }`}>
                              {caseItem.status === 'completed' ? '' :
                               caseItem.status === 'analyzing' ? '' :
                               caseItem.status}
                            </span>
                          )}
                        </div>
                        {/*   -  DARK THEME */}
                        <div className="flex items-center gap-2 mt-3">
                          <button
                            onClick={() => {
                              if (onUploadToCase) {
                                onUploadToCase(caseItem);
                              } else {
                                handleSelectCase(caseItem);
                              }
                            }}
                            className="px-4 py-1.5 bg-purple-600 text-white rounded-lg hover:bg-purple-700 transition-colors text-sm font-medium flex items-center gap-1"
                          >
                            <FileText size={16} />
                             
                          </button>
                          {caseItem.status === 'completed' && (
                            <button
                              onClick={(e) => {
                                e.stopPropagation();
                                handleSelectCase(caseItem);
                              }}
                              className="px-3 py-1.5 bg-blue-500/20 text-blue-400 border border-blue-500/30 rounded-lg hover:bg-blue-500/30 transition-colors text-sm font-medium flex items-center gap-1"
                              title="    "
                            >
                              <MessageCircle size={16} />
                              '  
                            </button>
                          )}
                        </div>
                      </div>
                    </div>
                  </div>
                ))}
              </div>
            ) : searchQuery ? (
              <div className="text-center py-8">
                <Search className="mx-auto mb-2 text-slate-500" size={48} />
                <p className="text-slate-400">     </p>
              </div>
            ) : (
              <div className="text-center py-8">
                <FileText className="mx-auto mb-2 text-slate-500" size={48} />
                <p className="text-slate-400">  </p>
              </div>
            )}
          </div>

          {/* Divider -  DARK THEME */}
          <div className="flex items-center gap-4 my-6">
            <div className="flex-1 h-px bg-white/10" />
            <span className="text-slate-500 text-sm"></span>
            <div className="flex-1 h-px bg-white/10" />
          </div>

          {/*  2:   -  DARK THEME */}
          <div>
            <div className="flex items-center gap-3 mb-4">
              <div className="w-10 h-10 bg-purple-500/20 rounded-full flex items-center justify-center border border-purple-500/30">
                <Plus className="text-purple-400" size={20} />
              </div>
              <div>
                <h3 className="text-lg font-bold text-white">  </h3>
                <p className="text-sm text-slate-400">    </p>
              </div>
            </div>

            <button
              onClick={handleNewCase}
              className="w-full p-4 bg-gradient-to-r from-purple-600 to-blue-600 text-white rounded-xl hover:from-purple-700 hover:to-blue-700 transition-all font-medium flex items-center justify-center gap-2 shadow-lg shadow-purple-500/20"
            >
              <Plus size={20} />
              <span>  </span>
            </button>
          </div>
        </div>
      </div>
    </div>
  );
};

export default CaseSelectorDialog;
