/**
 * InputWithHistory -     
 *
 *  CODE-003: Migrated to TypeScript
 *
 * Features:
 * - Autocomplete suggestions from user's input history
 * - Keyboard navigation (arrow keys, enter, escape)
 * - Click to select
 * - RTL support for Hebrew
 */

import React, { useState, useRef, useEffect, useCallback, type KeyboardEvent, type ChangeEvent, type FC } from 'react';
import type { LucideIcon } from 'lucide-react';

// ===========================
// Types
// ===========================

type InputVariant = 'dark' | 'light';

interface InputWithHistoryProps {
  value: string;
  onChange: (value: string) => void;
  suggestions?: string[];
  placeholder?: string;
  className?: string;
  inputClassName?: string;
  labelClassName?: string;
  disabled?: boolean;
  maxSuggestions?: number;
  minCharsToShow?: number;
  label?: string;
  required?: boolean;
  icon?: LucideIcon;
  variant?: InputVariant;
}

// ===========================
// Component
// ===========================

const InputWithHistory: FC<InputWithHistoryProps> = ({
  value,
  onChange,
  suggestions = [],
  placeholder = '',
  className = '',
  inputClassName = '',
  labelClassName = '',
  disabled = false,
  maxSuggestions = 8,
  minCharsToShow = 0,
  label,
  required = false,
  icon: Icon,
  variant = 'dark',
}) => {
  const [showSuggestions, setShowSuggestions] = useState(false);
  const [selectedIndex, setSelectedIndex] = useState(-1);
  const [filteredSuggestions, setFilteredSuggestions] = useState<string[]>([]);
  const inputRef = useRef<HTMLInputElement>(null);
  const containerRef = useRef<HTMLDivElement>(null);

  // Filter suggestions based on input
  useEffect(() => {
    if (!suggestions || suggestions.length === 0) {
      setFilteredSuggestions([]);
      return;
    }

    const inputValue = (value || '').toLowerCase().trim();

    // If input is short, show all recent suggestions
    if (inputValue.length < minCharsToShow) {
      setFilteredSuggestions(suggestions.slice(0, maxSuggestions));
    } else {
      // Filter by matching input
      const filtered = suggestions
        .filter(s => s.toLowerCase().includes(inputValue))
        .slice(0, maxSuggestions);
      setFilteredSuggestions(filtered);
    }
  }, [value, suggestions, maxSuggestions, minCharsToShow]);

  // Close suggestions when clicking outside
  useEffect(() => {
    const handleClickOutside = (event: MouseEvent): void => {
      if (containerRef.current && !containerRef.current.contains(event.target as Node)) {
        setShowSuggestions(false);
      }
    };

    document.addEventListener('mousedown', handleClickOutside);
    return () => document.removeEventListener('mousedown', handleClickOutside);
  }, []);

  const handleInputChange = useCallback((e: ChangeEvent<HTMLInputElement>): void => {
    onChange(e.target.value);
    setShowSuggestions(true);
    setSelectedIndex(-1);
  }, [onChange]);

  const handleFocus = useCallback((): void => {
    if (filteredSuggestions.length > 0) {
      setShowSuggestions(true);
    }
  }, [filteredSuggestions.length]);

  const handleSelectSuggestion = useCallback((suggestion: string): void => {
    onChange(suggestion);
    setShowSuggestions(false);
    setSelectedIndex(-1);
    inputRef.current?.focus();
  }, [onChange]);

  const handleKeyDown = useCallback((e: KeyboardEvent<HTMLInputElement>): void => {
    if (!showSuggestions || filteredSuggestions.length === 0) {
      return;
    }

    switch (e.key) {
      case 'ArrowDown':
        e.preventDefault();
        setSelectedIndex(prev =>
          prev < filteredSuggestions.length - 1 ? prev + 1 : 0
        );
        break;
      case 'ArrowUp':
        e.preventDefault();
        setSelectedIndex(prev =>
          prev > 0 ? prev - 1 : filteredSuggestions.length - 1
        );
        break;
      case 'Enter':
        if (selectedIndex >= 0 && selectedIndex < filteredSuggestions.length) {
          e.preventDefault();
          handleSelectSuggestion(filteredSuggestions[selectedIndex]);
        }
        break;
      case 'Escape':
        setShowSuggestions(false);
        setSelectedIndex(-1);
        break;
      case 'Tab':
        if (selectedIndex >= 0 && selectedIndex < filteredSuggestions.length) {
          handleSelectSuggestion(filteredSuggestions[selectedIndex]);
        }
        setShowSuggestions(false);
        break;
      default:
        break;
    }
  }, [showSuggestions, filteredSuggestions, selectedIndex, handleSelectSuggestion]);

  const shouldShowDropdown = showSuggestions && filteredSuggestions.length > 0;

  // Variant-based styling
  const isLight = variant === 'light';

  const baseLabelClass = isLight
    ? 'block text-sm font-semibold text-slate-700 mb-2'
    : 'block text-sm font-medium text-gray-300 mb-1';

  const baseInputClass = isLight
    ? 'w-full px-4 py-3 bg-white border border-slate-300 rounded-lg text-slate-900 placeholder-slate-400 focus:ring-2 focus:ring-blue-500 focus:border-transparent'
    : 'w-full px-4 py-3 bg-white/5 border border-white/10 rounded-xl text-white placeholder-white/40 focus:ring-2 focus:ring-purple-500/50 focus:border-purple-500/50';

  const baseIconClass = isLight ? 'text-slate-400' : 'text-gray-400';

  return (
    <div ref={containerRef} className={`relative ${className}`}>
      {label && (
        <label className={labelClassName || baseLabelClass}>
          {label}
          {required && <span className="text-red-500 mr-1">*</span>}
        </label>
      )}

      <div className="relative">
        {Icon && (
          <div className={`absolute right-3 top-1/2 -translate-y-1/2 ${baseIconClass} pointer-events-none`}>
            <Icon size={18} />
          </div>
        )}

        <input
          ref={inputRef}
          type="text"
          value={value}
          onChange={handleInputChange}
          onFocus={handleFocus}
          onKeyDown={handleKeyDown}
          placeholder={placeholder}
          disabled={disabled}
          className={`${baseInputClass} transition-all
            ${Icon ? 'pr-10' : ''}
            ${disabled ? 'opacity-50 cursor-not-allowed' : ''}
            ${inputClassName}`}
          autoComplete="off"
        />

        {/* History indicator */}
        {suggestions.length > 0 && !disabled && (
          <button
            type="button"
            onClick={() => setShowSuggestions(!showSuggestions)}
            className="absolute left-3 top-1/2 -translate-y-1/2 text-gray-400
              hover:text-purple-400 transition-colors p-1"
            title=""
          >
            <svg width="16" height="16" viewBox="0 0 24 24" fill="none" stroke="currentColor" strokeWidth="2">
              <circle cx="12" cy="12" r="10" />
              <polyline points="12 6 12 12 16 14" />
            </svg>
          </button>
        )}
      </div>

      {/* Suggestions dropdown */}
      {shouldShowDropdown && (
        <div className="absolute z-50 w-full mt-1 bg-gray-800 border border-white/20
          rounded-xl shadow-lg overflow-hidden max-h-60 overflow-y-auto">
          <div className="text-xs text-gray-400 px-3 py-1.5 border-b border-white/10">
             
          </div>
          {filteredSuggestions.map((suggestion, index) => (
            <button
              key={index}
              type="button"
              onClick={() => handleSelectSuggestion(suggestion)}
              onMouseEnter={() => setSelectedIndex(index)}
              className={`w-full text-right px-4 py-2.5 text-white transition-colors
                ${index === selectedIndex
                  ? 'bg-purple-500/30 text-purple-200'
                  : 'hover:bg-white/10'
                }`}
            >
              <span className="truncate block">{suggestion}</span>
            </button>
          ))}
        </div>
      )}
    </div>
  );
};

export default InputWithHistory;
