                            <span className="text-red-400 text-sm"> </span>

                            {/*    */}
                            <button
                              onClick={() => retryCase(c.id)}
                              className="px-2 py-1 text-green-400 hover:text-green-300 text-sm flex items-center gap-1"
                              title=" "
                            >
                              <RefreshCw size={14} />
                            </button>

