/**
 * ErrorDisplay -     retry
 * 
 *        
 */

import React from 'react';
import { AlertCircle, RefreshCw, X } from 'lucide-react';

interface ErrorDisplayProps {
  error: string | Error | null;
  onRetry?: () => void;
  onDismiss?: () => void;
  title?: string;
  retryText?: string;
  dismissText?: string;
  showRetry?: boolean;
  showDismiss?: boolean;
  className?: string;
}

const ErrorDisplay: React.FC<ErrorDisplayProps> = ({
  error,
  onRetry,
  onDismiss,
  title = '',
  retryText = ' ',
  dismissText = '',
  showRetry = true,
  showDismiss = true,
  className = ''
}) => {
  if (!error) return null;

  const errorMessage = typeof error === 'string' ? error : error.message || '  ';

  return (
    <div className={`bg-red-500/20 border border-red-500/50 rounded-xl p-4 ${className}`} dir="rtl">
      <div className="flex items-start gap-3">
        <div className="flex-shrink-0 w-10 h-10 bg-red-500/20 rounded-lg flex items-center justify-center">
          <AlertCircle className="text-red-400" size={20} />
        </div>
        
        <div className="flex-1 min-w-0">
          <div className="flex items-center justify-between mb-2">
            <h3 className="text-red-200 font-semibold text-lg">{title}</h3>
            {showDismiss && onDismiss && (
              <button
                onClick={onDismiss}
                className="p-1 hover:bg-red-500/20 rounded-lg transition-colors text-red-300 hover:text-red-200"
                title={dismissText}
              >
                <X size={18} />
              </button>
            )}
          </div>
          <p className="text-red-200 text-sm leading-relaxed">{errorMessage}</p>
          
          {showRetry && onRetry && (
            <button
              onClick={onRetry}
              className="mt-3 px-4 py-2 bg-red-600 hover:bg-red-700 text-white rounded-lg font-semibold transition-all flex items-center gap-2 text-sm"
            >
              <RefreshCw size={16} />
              {retryText}
            </button>
          )}
        </div>
      </div>
    </div>
  );
};

export default ErrorDisplay;
