/**
 * NewCaseWizard -     
 *
 *  :
 * 1.   ( )
 * 2.    NotebookLLM
 * 3.      
 */

import React, { useState, useEffect } from 'react';
import {
  FileText,
  Users,
  Scale,
  Upload,
  ChevronRight,
  ChevronLeft,
  Check,
  Plus,
  X,
  AlertCircle,
  User,
  Briefcase,
  Gavel
} from 'lucide-react';

//  P0 FIX: Use centralized API utility
import { apiPost, apiUpload, apiGet } from '../utils/api';
import { logger } from '../utils/logger';
import InputWithHistory from './InputWithHistory';
import { toast } from 'sonner';
//  Dec 2024: Connect to global progress tracking
import {
  startUploadJob,
  failUploadJob,
} from './GlobalUploadProgress';

//  Simplified: Documents sent to NotebookLLM for analysis

const NewCaseWizard = ({ onClose, onCaseCreated }) => {
  // Start directly at document upload step
  const [currentStep, setCurrentStep] = useState(4);
  const [loading, setLoading] = useState(false);
  const [error, setError] = useState(null);

  // Step 1: Basic Info
  const [caseNumber, setCaseNumber] = useState('');
  const [caseName, setCaseName] = useState('');
  const [court, setCourt] = useState('');
  const [caseType, setCaseType] = useState('');
  const [judgeName, setJudgeName] = useState('');  //  /

  // Step 2: Parties
  const [myRole, setMyRole] = useState('plaintiff');
  const [myName, setMyName] = useState('');
  const [myDescription, setMyDescription] = useState('');
  const [myLawyerName, setMyLawyerName] = useState('');  //    
  const [opponentRole, setOpponentRole] = useState('defendant');
  const [opponentName, setOpponentName] = useState('');
  const [opponentDescription, setOpponentDescription] = useState('');
  const [opponentLawyerName, setOpponentLawyerName] = useState('');  //     

  // Witnesses
  const [myWitnesses, setMyWitnesses] = useState([]);
  const [opponentWitnesses, setOpponentWitnesses] = useState([]);

  // Step 3: Context - Enhanced Case Briefing
  const [caseContext, setCaseContext] = useState('');
  const [disputeSummary, setDisputeSummary] = useState('');
  const [reliefSought, setReliefSought] = useState('');  //   
  const [opponentMainArgument, setOpponentMainArgument] = useState('');  //   
  const [myStrengths, setMyStrengths] = useState('');  //   
  const [myWeaknesses, setMyWeaknesses] = useState('');  //   
  const [opponentWeaknesses, setOpponentWeaknesses] = useState('');  //    
  const [keyIssues, setKeyIssues] = useState('');  //  

  // Step 4: Files
  const [selectedFiles, setSelectedFiles] = useState([]);

  //  Input History for autocomplete
  const [inputHistory, setInputHistory] = useState({
    party_names: [],
    lawyer_names: [],
    court_names: [],
    judge_names: [],
    case_numbers: [],
    witness_names: [],
  });

  // Load input history on mount
  useEffect(() => {
    const loadInputHistory = async () => {
      try {
        const response = await apiGet('/api/auth/input-history');
        if (response?.input_history) {
          setInputHistory(response.input_history);
          logger.log(' Loaded input history');
        }
      } catch (err) {
        logger.warn('Failed to load input history:', err);
        // Non-critical - continue without history
      }
    };
    loadInputHistory();
  }, []);

  // Save input history after successful case creation
  const saveInputHistory = async () => {
    const items = [];

    // Collect all values to save
    if (myName.trim()) items.push({ field: 'party_names', value: myName.trim() });
    if (opponentName.trim()) items.push({ field: 'party_names', value: opponentName.trim() });
    if (myLawyerName.trim()) items.push({ field: 'lawyer_names', value: myLawyerName.trim() });
    if (opponentLawyerName.trim()) items.push({ field: 'lawyer_names', value: opponentLawyerName.trim() });
    if (court.trim()) items.push({ field: 'court_names', value: court.trim() });
    if (judgeName.trim()) items.push({ field: 'judge_names', value: judgeName.trim() });
    if (caseNumber.trim()) items.push({ field: 'case_numbers', value: caseNumber.trim() });

    // Save witness names
    myWitnesses.forEach(w => {
      if (w.name?.trim()) items.push({ field: 'witness_names', value: w.name.trim() });
    });
    opponentWitnesses.forEach(w => {
      if (w.name?.trim()) items.push({ field: 'witness_names', value: w.name.trim() });
    });

    // Save per-file lawyer names
    selectedFiles.forEach(f => {
      if (f.opponent_lawyer_name?.trim()) {
        items.push({ field: 'lawyer_names', value: f.opponent_lawyer_name.trim() });
      }
    });

    if (items.length > 0) {
      try {
        await apiPost('/api/auth/input-history/batch', { items });
        logger.log(` Saved ${items.length} items to input history`);
      } catch (err) {
        logger.warn('Failed to save input history:', err);
      }
    }
  };

  const roleOptions = [
    { value: 'plaintiff', label: '', opposite: 'defendant' },
    { value: 'defendant', label: '', opposite: 'plaintiff' },
    { value: 'petitioner', label: '', opposite: 'respondent' },
    { value: 'respondent', label: '', opposite: 'petitioner' },
    { value: 'appellant', label: '', opposite: 'respondent' },
    { value: 'counter_plaintiff', label: ' ', opposite: 'counter_defendant' },
    { value: 'counter_defendant', label: ' ', opposite: 'counter_plaintiff' },
    { value: 'plaintiff_counter_defendant', label: ' +  ', opposite: 'defendant_counter_plaintiff' },
    { value: 'defendant_counter_plaintiff', label: ' +  ', opposite: 'plaintiff_counter_defendant' }
  ];

  const caseTypes = [
    ' ',
    ' ',
    ' ',
    '',
    '',
    '',
    '',
    '',
    ''
  ];

  const courts = [
    '  ',
    '  ',
    '  ',
    '  ',
    '   ',
    '   ',
    ''
  ];

  // Witnesses helpers
  const addWitness = (side) => {
    const newWitness = { name: '', role: '', description: '' };
    if (side === 'my') {
      setMyWitnesses([...myWitnesses, newWitness]);
    } else {
      setOpponentWitnesses([...opponentWitnesses, newWitness]);
    }
  };

  const removeWitness = (side, index) => {
    if (side === 'my') {
      setMyWitnesses(myWitnesses.filter((_, i) => i !== index));
    } else {
      setOpponentWitnesses(opponentWitnesses.filter((_, i) => i !== index));
    }
  };

  const updateWitness = (side, index, field, value) => {
    if (side === 'my') {
      const updated = [...myWitnesses];
      updated[index][field] = value;
      setMyWitnesses(updated);
    } else {
      const updated = [...opponentWitnesses];
      updated[index][field] = value;
      setOpponentWitnesses(updated);
    }
  };

  // File handling - Simplified
  const handleFileSelect = (e) => {
    const files = Array.from(e.target.files || []);
    setSelectedFiles((prev) => [
      ...prev,
      ...files.map((f) => ({
        id: Date.now() + Math.random(),
        file: f,
        name: f.name,
        size: (f.size / 1024 / 1024).toFixed(2) + ' MB'
      }))
    ]);
  };

  const removeFile = (id) => {
    setSelectedFiles((prev) => prev.filter((f) => f.id !== id));
  };

  // Validation
  const validateStep = () => {
    if (currentStep === 1) {
      if (!caseName.trim()) {
        setError('   ');
        return false;
      }
    }
    if (currentStep === 2) {
      if (!myName.trim()) {
        setError('   / ');
        return false;
      }
      if (!opponentName.trim()) {
        setError('    ');
        return false;
      }
    }
    if (currentStep === 3) {
      if (!disputeSummary.trim()) {
        setError('     ');
        return false;
      }
    }
    if (currentStep === 4) {
      //  Simplified: Just require at least one file
      if (selectedFiles.length === 0) {
        setError('    ');
        return false;
      }
    }
    setError(null);
    return true;
  };

  // Navigation
  const nextStep = () => {
    if (validateStep()) {
      setCurrentStep((prev) => Math.min(prev + 1, 4));
    }
  };

  const prevStep = () => {
    setError(null);
    setCurrentStep((prev) => Math.max(prev - 1, 1));
  };

  // Submit
  const handleSubmit = async () => {
    if (!validateStep()) return;

    setLoading(true);
    setError(null);

    try {
      // Auto-generate case name from first document if not provided
      const autoName = caseName.trim() ||
        (selectedFiles.length > 0
          ? ` ${new Date().toLocaleDateString('he-IL')} - ${selectedFiles[0].name.replace(/\.[^/.]+$/, '').slice(0, 30)}`
          : `  - ${new Date().toLocaleDateString('he-IL')}`);

      //  Create case with minimal data - details will be extracted from documents
      const caseData = {
        name: autoName,
        case_number: caseNumber || '',
        court: court || '',
        case_type: caseType || 'general'
      };

      const caseResult = await apiPost('/api/cases', caseData, { showErrorToast: false });
      const { case: newCase, warnings } = caseResult;
      const caseId = newCase.id;

      //  Dec 2024: Show limit warnings to user
      if (warnings && warnings.length > 0) {
        warnings.forEach((warning) => {
          toast.warning(warning.message, {
            duration: 8000,
            icon: '',
            description: warning.percent >= 90
              ? '      '
              : undefined
          });
        });
      }

      // Step 2: Save context with enhanced briefing data
      //  Parse judge names (comma-separated) into array format
      const judgesArray = judgeName.trim()
        ? judgeName.split(',').map(name => ({ name: name.trim(), role: '' })).filter(j => j.name)
        : [];

      //  FIX: Only include party info if name is provided (wizard starts at step 4, skipping party info)
      const contextData = {
        //  Court info wrapped in object as backend expects
        court_info: (court || caseNumber) ? {
          court_name: court || null,
          case_number: caseNumber || null
        } : null,
        // Only include my_side if party name is provided
        my_side: myName.trim() ? {
          party_name: myName.trim(),
          party_role: myRole,
          party_description: myDescription || null,
          lawyer_name: myLawyerName || null
        } : null,
        // Only include opposing_side if party name is provided
        opposing_side: opponentName.trim() ? {
          party_name: opponentName.trim(),
          party_role: opponentRole,
          party_description: opponentDescription || null,
          lawyer_name: opponentLawyerName || null
        } : null,
        my_witnesses: myWitnesses.filter((w) => w.name.trim()),
        opposing_witnesses: opponentWitnesses.filter((w) => w.name.trim()),
        judges: judgesArray,

        //  NEW: Enhanced Case Briefing
        case_briefing: {
          dispute_summary: disputeSummary.trim() || null,
          relief_sought: reliefSought.trim() || null,
          opponent_main_argument: opponentMainArgument.trim() || null,
          key_issues: keyIssues.trim() || null,
          my_strengths: myStrengths.trim() || null,
          my_weaknesses: myWeaknesses.trim() || null,
          opponent_weaknesses: opponentWeaknesses.trim() || null,
          additional_context: caseContext.trim() || null
        },

        // Legacy field for backward compatibility
        case_context: `${disputeSummary}\n\n${caseContext}`.trim()
      };

      //  P0 FIX: Use centralized API utility
      try {
        await apiPost(`/api/cases/${caseId}/context`, contextData, { showErrorToast: false });
      } catch (err) {
        logger.warn('Failed to save context, but case created:', err);
      }

      // Step 3: Upload files and start analysis (if any)
      if (selectedFiles.length > 0) {
        //  Dec 2024: Start global progress tracking
        // Use case-{caseId} format so the WebSocket monitor knows the case ID
        const jobId = `case-${caseId}`;
        startUploadJob({
          id: jobId,
          type: 'case',
          name: caseName,
          totalFiles: selectedFiles.length,
          processedFiles: 0,
          documents: selectedFiles.map(f => ({
            filename: f.name,
            status: 'pending',
            canRetry: true
          }))
        });

        const formData = new FormData();

        //  Simplified: Just upload files for NotebookLLM analysis
        selectedFiles.forEach((f) => {
          formData.append('files', f.file);
        });

        //  Backend expects JSON arrays: file_sources / document_types / opponent_lawyer_names
        // Wizard doesn't collect per-file metadata yet, so default to "self"/"other"/"".
        formData.append('file_sources', JSON.stringify(selectedFiles.map(() => 'self')));
        formData.append('document_types', JSON.stringify(selectedFiles.map(() => 'other')));
        formData.append('opponent_lawyer_names', JSON.stringify(selectedFiles.map(() => '')));

        try {
          //  P0 FIX: Use centralized API utility
          await apiUpload(`/api/cases/${caseId}/files`, formData, { showErrorToast: false });
          // Upload started successfully - WebSocket will handle progress updates
          logger.log(`[NewCaseWizard] Files uploaded, tracking progress for job ${jobId}`);
        } catch (uploadErr) {
          // Mark job as failed
          failUploadJob(jobId, uploadErr.message || '  ', true);
          throw uploadErr;
        }
      }

      // Success!
      //  Save input history for autocomplete
      await saveInputHistory();

      //  FIX: Dispatch caseCreated event to notify Sidebar to refresh
      window.dispatchEvent(new CustomEvent('caseCreated', {
        detail: { caseId: caseId, caseName: autoName }
      }));

      if (onCaseCreated) {  //  : onSuccess  onCaseCreated
        onCaseCreated(newCase.id || newCase);  //    -ID
      }

      if (onClose) {
        onClose();
      }

      //  FIX: Only do hard redirect if onCaseCreated is not provided
      // (onCaseCreated already handles navigation via React Router)
      if (!onCaseCreated) {
        window.location.href = `/cases/${caseId}`;
      }
    } catch (err) {
      //  FLOW-105 FIX: Better error message with guidance
      const errorMsg = err.message || '  ';
      setError(`  : ${errorMsg}.         .`);
    } finally {
      setLoading(false);
    }
  };

  // Update opponent role when my role changes
  const handleMyRoleChange = (newRole) => {
    setMyRole(newRole);
    const roleOption = roleOptions.find((r) => r.value === newRole);
    if (roleOption) {
      setOpponentRole(roleOption.opposite);
    }
  };

  return (
    <div className="fixed inset-0 bg-black/70 backdrop-blur-sm flex items-center justify-center z-50 p-4 overflow-y-auto">
      <div className="bg-white rounded-2xl shadow-2xl max-w-4xl w-full max-h-[90vh] overflow-y-auto">
        {/* Header */}
        <div className="sticky top-0 bg-gradient-to-r from-blue-600 to-purple-600 text-white p-6 rounded-t-2xl">
          <div className="flex items-center justify-between">
            <h2 className="text-2xl font-bold">   </h2>
            <button
              onClick={onClose}
              className="p-2 hover:bg-white/20 rounded-lg transition-colors"
            >
              <X size={24} />
            </button>
          </div>

          {/* Simplified header - no steps needed */}
          <div className="mt-4">
            <p className="text-blue-100 text-sm">
                 ( ,  ')     
            </p>
          </div>
        </div>

        {/* Content */}
        <div className="p-8">
          {error && (
            <div className="mb-6 p-4 bg-red-50 border border-red-200 rounded-lg flex items-start gap-3">
              <AlertCircle className="text-red-500 flex-shrink-0 mt-0.5" size={20} />
              <p className="text-red-700">{error}</p>
            </div>
          )}

          {/* Step 1: Basic Info */}
          {currentStep === 1 && (
            <div className="space-y-6">
              <div className="mb-6">
                <h3 className="text-xl font-bold text-slate-800 mb-2">   </h3>
                <p className="text-slate-600 text-sm"> FLOW-103 FIX:      .      </p>
              </div>

              <div>
                <label className="block text-sm font-semibold text-slate-700 mb-2">
                    *
                </label>
                <input
                  type="text"
                  value={caseName}
                  onChange={(e) => setCaseName(e.target.value)}
                  placeholder=":     ABC"
                  className="w-full px-4 py-3 border border-slate-300 rounded-lg focus:ring-2 focus:ring-blue-500 focus:border-transparent"
                />
              </div>

              <div>
                <label className="block text-sm font-semibold text-slate-700 mb-2">
                    ()
                </label>
                <input
                  type="text"
                  value={caseNumber}
                  onChange={(e) => setCaseNumber(e.target.value)}
                  placeholder=":  53452-12-25"
                  className="w-full px-4 py-3 border border-slate-300 rounded-lg focus:ring-2 focus:ring-blue-500 focus:border-transparent"
                />
              </div>

              <div className="grid grid-cols-2 gap-4">
                <div>
                  <label className="block text-sm font-semibold text-slate-700 mb-2">
                     
                  </label>
                  <select
                    value={court}
                    onChange={(e) => setCourt(e.target.value)}
                    className="w-full px-4 py-3 border border-slate-300 rounded-lg focus:ring-2 focus:ring-blue-500 focus:border-transparent"
                  >
                    <option value="">  </option>
                    {courts.map((c) => (
                      <option key={c} value={c}>
                        {c}
                      </option>
                    ))}
                  </select>
                </div>

                <div>
                  <label className="block text-sm font-semibold text-slate-700 mb-2">
                     
                  </label>
                  <select
                    value={caseType}
                    onChange={(e) => setCaseType(e.target.value)}
                    className="w-full px-4 py-3 border border-slate-300 rounded-lg focus:ring-2 focus:ring-blue-500 focus:border-transparent"
                  >
                    <option value="">  </option>
                    {caseTypes.map((t) => (
                      <option key={t} value={t}>
                        {t}
                      </option>
                    ))}
                  </select>
                </div>
              </div>

              {/* Judge Name */}
              <div>
                <InputWithHistory
                  value={judgeName}
                  onChange={setJudgeName}
                  suggestions={inputHistory.judge_names}
                  label=" / ()"
                  placeholder=": '   "
                  icon={Gavel}
                  variant="light"
                />
                <p className="text-sm text-slate-500 mt-1">     </p>
              </div>
            </div>
          )}

          {/* Step 2: Parties & Witnesses */}
          {currentStep === 2 && (
            <div className="space-y-6">
              <div className="mb-6">
                <h3 className="text-xl font-bold text-slate-800 mb-2">   ?</h3>
                <p className="text-slate-600 text-sm"> FLOW-103 FIX:    .        </p>
              </div>

              {/* My Side */}
              <div className="bg-blue-50 border border-blue-200 rounded-lg p-6">
                <h4 className="font-bold text-blue-900 mb-4">  </h4>

                <div className="space-y-4">
                  <div>
                    <label className="block text-sm font-semibold text-slate-700 mb-2">
                         ? *
                    </label>
                    <select
                      value={myRole}
                      onChange={(e) => handleMyRoleChange(e.target.value)}
                      className="w-full px-4 py-3 border border-slate-300 rounded-lg focus:ring-2 focus:ring-blue-500"
                    >
                      {roleOptions.map((r) => (
                        <option key={r.value} value={r.value}>
                          {r.label}
                        </option>
                      ))}
                    </select>
                  </div>

                  <div>
                    <InputWithHistory
                      value={myName}
                      onChange={setMyName}
                      suggestions={inputHistory.party_names}
                      label=" ( / ) *"
                      placeholder=":  XYZ "
                      icon={User}
                      variant="light"
                      required
                    />
                  </div>

                  <div>
                    <label className="block text-sm font-semibold text-slate-700 mb-2">
                        ()
                    </label>
                    <textarea
                      value={myDescription}
                      onChange={(e) => setMyDescription(e.target.value)}
                      placeholder=":     "
                      rows={2}
                      className="w-full px-4 py-3 border border-slate-300 rounded-lg focus:ring-2 focus:ring-blue-500"
                    />
                  </div>

                  <div>
                    <InputWithHistory
                      value={myLawyerName}
                      onChange={setMyLawyerName}
                      suggestions={inputHistory.lawyer_names}
                      label="     ()"
                      placeholder=":   "
                      icon={Briefcase}
                      variant="light"
                    />
                  </div>

                  {/* My Witnesses */}
                  <div>
                    <div className="flex items-center justify-between mb-2">
                      <label className="block text-sm font-semibold text-slate-700">
                          
                      </label>
                      <button
                        onClick={() => addWitness('my')}
                        className="flex items-center gap-1 text-sm text-blue-600 hover:text-blue-700"
                      >
                        <Plus size={16} />
                         
                      </button>
                    </div>

                    {myWitnesses.map((witness, idx) => (
                      <div key={idx} className="flex gap-2 mb-2">
                        <input
                          type="text"
                          value={witness.name}
                          onChange={(e) => updateWitness('my', idx, 'name', e.target.value)}
                          placeholder=" "
                          className="flex-1 px-3 py-2 border border-slate-300 rounded-lg text-sm"
                        />
                        <button
                          onClick={() => removeWitness('my', idx)}
                          className="p-2 text-red-500 hover:bg-red-50 rounded-lg"
                        >
                          <X size={18} />
                        </button>
                      </div>
                    ))}
                  </div>
                </div>
              </div>

              {/* Opponent Side */}
              <div className="bg-red-50 border border-red-200 rounded-lg p-6">
                <h4 className="font-bold text-red-900 mb-4">  </h4>

                <div className="space-y-4">
                  <div>
                    <label className="block text-sm font-semibold text-slate-700 mb-2">
                         *
                    </label>
                    <select
                      value={opponentRole}
                      onChange={(e) => setOpponentRole(e.target.value)}
                      className="w-full px-4 py-3 border border-slate-300 rounded-lg focus:ring-2 focus:ring-red-500"
                    >
                      {roleOptions.map((r) => (
                        <option key={r.value} value={r.value}>
                          {r.label}
                        </option>
                      ))}
                    </select>
                  </div>

                  <div>
                    <InputWithHistory
                      value={opponentName}
                      onChange={setOpponentName}
                      suggestions={inputHistory.party_names}
                      label="   *"
                      placeholder=":  ABC "
                      icon={User}
                      variant="light"
                      required
                    />
                  </div>

                  <div>
                    <label className="block text-sm font-semibold text-slate-700 mb-2">
                        ()
                    </label>
                    <textarea
                      value={opponentDescription}
                      onChange={(e) => setOpponentDescription(e.target.value)}
                      placeholder=":    "
                      rows={2}
                      className="w-full px-4 py-3 border border-slate-300 rounded-lg focus:ring-2 focus:ring-red-500"
                    />
                  </div>

                  <div>
                    <InputWithHistory
                      value={opponentLawyerName}
                      onChange={setOpponentLawyerName}
                      suggestions={inputHistory.lawyer_names}
                      label="      ()"
                      placeholder=":   "
                      icon={Briefcase}
                      variant="light"
                    />
                  </div>

                  {/* Opponent Witnesses */}
                  <div>
                    <div className="flex items-center justify-between mb-2">
                      <label className="block text-sm font-semibold text-slate-700">
                          
                      </label>
                      <button
                        onClick={() => addWitness('opponent')}
                        className="flex items-center gap-1 text-sm text-red-600 hover:text-red-700"
                      >
                        <Plus size={16} />
                         
                      </button>
                    </div>

                    {opponentWitnesses.map((witness, idx) => (
                      <div key={idx} className="flex gap-2 mb-2">
                        <input
                          type="text"
                          value={witness.name}
                          onChange={(e) =>
                            updateWitness('opponent', idx, 'name', e.target.value)
                          }
                          placeholder=" "
                          className="flex-1 px-3 py-2 border border-slate-300 rounded-lg text-sm"
                        />
                        <button
                          onClick={() => removeWitness('opponent', idx)}
                          className="p-2 text-red-500 hover:bg-red-50 rounded-lg"
                        >
                          <X size={18} />
                        </button>
                      </div>
                    ))}
                  </div>
                </div>
              </div>
            </div>
          )}

          {/* Step 3: Context - Enhanced Case Briefing */}
          {currentStep === 3 && (
            <div className="space-y-6">
              <div className="mb-6">
                <h3 className="text-xl font-bold text-slate-800 mb-2">  </h3>
                <p className="text-slate-600 text-sm">
                      ,            
                </p>
              </div>

              {/* Core dispute */}
              <div className="bg-blue-50 border border-blue-200 rounded-lg p-4">
                <label className="block text-sm font-bold text-blue-900 mb-2">
                     * (2-3 )
                </label>
                <textarea
                  value={disputeSummary}
                  onChange={(e) => setDisputeSummary(e.target.value)}
                  placeholder=":      450,000   ,         .          ."
                  rows={4}
                  className="w-full px-4 py-3 border border-blue-300 rounded-lg focus:ring-2 focus:ring-blue-500 bg-white"
                />
              </div>

              {/* Relief sought */}
              <div>
                <label className="block text-sm font-semibold text-slate-700 mb-2">
                     /   ?
                </label>
                <textarea
                  value={reliefSought}
                  onChange={(e) => setReliefSought(e.target.value)}
                  placeholder=":  450,000  +     ,    "
                  rows={2}
                  className="w-full px-4 py-3 border border-slate-300 rounded-lg focus:ring-2 focus:ring-blue-500"
                />
              </div>

              {/* Opponent's main argument */}
              <div className="bg-red-50 border border-red-200 rounded-lg p-4">
                <label className="block text-sm font-bold text-red-900 mb-2">
                        ?
                </label>
                <textarea
                  value={opponentMainArgument}
                  onChange={(e) => setOpponentMainArgument(e.target.value)}
                  placeholder=":        ,   ,       "
                  rows={3}
                  className="w-full px-4 py-3 border border-red-300 rounded-lg focus:ring-2 focus:ring-red-500 bg-white"
                />
                <p className="text-xs text-red-700 mt-2">
                     -       ,      
                </p>
              </div>

              {/* Key issues / disputed facts */}
              <div>
                <label className="block text-sm font-semibold text-slate-700 mb-2">
                     ? (  )
                </label>
                <textarea
                  value={keyIssues}
                  onChange={(e) => setKeyIssues(e.target.value)}
                  placeholder=": 1.      ? 2.     ? 3.    ?"
                  rows={3}
                  className="w-full px-4 py-3 border border-slate-300 rounded-lg focus:ring-2 focus:ring-blue-500"
                />
              </div>

              {/* Strengths and weaknesses - collapsible */}
              <div className="border border-slate-200 rounded-lg overflow-hidden">
                <details className="group">
                  <summary className="px-4 py-3 bg-slate-50 cursor-pointer hover:bg-slate-100 flex items-center justify-between">
                    <span className="font-semibold text-slate-700">    ( )</span>
                    <span className="text-slate-400 group-open:rotate-180 transition-transform"></span>
                  </summary>
                  <div className="p-4 space-y-4">
                    <div className="bg-green-50 border border-green-200 rounded-lg p-3">
                      <label className="block text-sm font-semibold text-green-800 mb-2">
                           
                      </label>
                      <textarea
                        value={myStrengths}
                        onChange={(e) => setMyStrengths(e.target.value)}
                        placeholder=":    ,    ,   "
                        rows={2}
                        className="w-full px-3 py-2 border border-green-300 rounded-lg text-sm bg-white"
                      />
                    </div>
                    <div className="bg-amber-50 border border-amber-200 rounded-lg p-3">
                      <label className="block text-sm font-semibold text-amber-800 mb-2">
                            (  )
                      </label>
                      <textarea
                        value={myWeaknesses}
                        onChange={(e) => setMyWeaknesses(e.target.value)}
                        placeholder=":     ,    "
                        rows={2}
                        className="w-full px-3 py-2 border border-amber-300 rounded-lg text-sm bg-white"
                      />
                      <p className="text-xs text-amber-700 mt-1">
                                  
                      </p>
                    </div>
                    <div className="bg-purple-50 border border-purple-200 rounded-lg p-3">
                      <label className="block text-sm font-semibold text-purple-800 mb-2">
                            
                      </label>
                      <textarea
                        value={opponentWeaknesses}
                        onChange={(e) => setOpponentWeaknesses(e.target.value)}
                        placeholder=":     ,   ,   "
                        rows={2}
                        className="w-full px-3 py-2 border border-purple-300 rounded-lg text-sm bg-white"
                      />
                    </div>
                  </div>
                </details>
              </div>

              {/* Additional context */}
              <div>
                <label className="block text-sm font-semibold text-slate-700 mb-2">
                     ()
                </label>
                <textarea
                  value={caseContext}
                  onChange={(e) => setCaseContext(e.target.value)}
                  placeholder="     -   ,  , '"
                  rows={3}
                  className="w-full px-4 py-3 border border-slate-300 rounded-lg focus:ring-2 focus:ring-blue-500"
                />
              </div>
            </div>
          )}

          {/* Step 4: Files - Simplified upload */}
          {currentStep === 4 && (
            <div className="space-y-6">
              <div className="mb-6">
                <h3 className="text-xl font-bold text-slate-800 mb-2">  </h3>
                <p className="text-slate-600 text-sm">
                      .         .
                </p>
              </div>

              {/* Simple info box */}
              <div className="bg-blue-50 border border-blue-200 rounded-lg p-4 text-sm text-blue-900">
                <p className="font-semibold mb-2">   :</p>
                <ul className="list-disc list-inside space-y-1 mr-2">
                  <li>  PDF, Word,  </li>
                  <li>   ,      </li>
                  <li>       '</li>
                </ul>
              </div>

              <div className="border-2 border-dashed border-slate-300 rounded-lg p-8 text-center hover:border-blue-500 transition-colors">
                <Upload className="mx-auto text-slate-400 mb-4" size={48} />
                <input
                  type="file"
                  multiple
                  accept=".pdf,.png,.jpg,.jpeg,.txt,.docx"
                  onChange={handleFileSelect}
                  className="hidden"
                  id="file-upload-wizard"
                />
                <label
                  htmlFor="file-upload-wizard"
                  className="cursor-pointer text-blue-600 hover:text-blue-700 font-semibold"
                >
                    
                </label>
                <p className="text-slate-500 text-sm mt-2">PDF, Word, PNG, JPG, TXT</p>
              </div>

              {selectedFiles.length > 0 && (
                <div className="space-y-3">
                  <p className="font-semibold text-slate-700">
                      ({selectedFiles.length})
                  </p>
                  {selectedFiles.map((file) => (
                    <div
                      key={file.id}
                      className="bg-slate-50 p-3 rounded-lg border border-slate-200 flex items-center justify-between"
                    >
                      <div className="flex items-center gap-3 flex-1 min-w-0">
                        <FileText className="text-blue-500 flex-shrink-0" size={20} />
                        <div className="flex-1 min-w-0">
                          <p className="text-sm font-semibold text-slate-800 truncate">{file.name}</p>
                          <p className="text-xs text-slate-500">{file.size}</p>
                        </div>
                      </div>
                      <button
                        onClick={() => removeFile(file.id)}
                        className="text-red-500 hover:text-red-600 flex-shrink-0 ml-2"
                      >
                        <X size={20} />
                      </button>
                    </div>
                  ))}
                </div>
              )}
            </div>
          )}
        </div>

        {/* Footer - Simplified for single step */}
        <div className="sticky bottom-0 bg-slate-50 border-t border-slate-200 p-6 rounded-b-2xl">
          <div className="flex items-center justify-between">
            <button
              onClick={onClose}
              className="flex items-center gap-2 px-6 py-3 bg-white border border-slate-300 rounded-lg hover:bg-slate-50 transition-colors"
            >
              
            </button>

            <button
              onClick={handleSubmit}
              disabled={loading || selectedFiles.length === 0}
              className="flex items-center gap-2 px-8 py-3 bg-gradient-to-r from-green-500 to-emerald-600 text-white rounded-lg hover:from-green-600 hover:to-emerald-700 transition-all font-semibold disabled:opacity-50 disabled:cursor-not-allowed"
            >
              {loading ? (
                <>
                  <div className="animate-spin rounded-full h-5 w-5 border-b-2 border-white" />
                   ...
                </>
              ) : (
                <>
                  <Check size={20} />
                   
                </>
              )}
            </button>
          </div>

          <p className="text-center text-xs text-slate-500 mt-4">
            {selectedFiles.length === 0 ? '  ' : `${selectedFiles.length}   `}
          </p>
        </div>
      </div>
    </div>
  );
};

export default NewCaseWizard;
