/**
 * ConfirmationModal -     
 * 
 *   window.confirm  modal  
 */

import React from 'react';
import { X, AlertTriangle, Trash2, CheckCircle } from 'lucide-react';

interface ConfirmationModalProps {
  isOpen: boolean;
  onClose: () => void;
  onConfirm: () => void;
  title: string;
  message: string;
  confirmText?: string;
  cancelText?: string;
  type?: 'danger' | 'warning' | 'info';
  icon?: React.ReactNode;
  isLoading?: boolean;
}

const ConfirmationModal: React.FC<ConfirmationModalProps> = ({
  isOpen,
  onClose,
  onConfirm,
  title,
  message,
  confirmText = '',
  cancelText = '',
  type = 'danger',
  icon,
  isLoading = false
}) => {
  if (!isOpen) return null;

  const getTypeStyles = () => {
    switch (type) {
      case 'danger':
        return {
          gradient: 'from-red-600 to-red-800',
          button: 'bg-red-600 hover:bg-red-700',
          iconBg: 'bg-red-500/20',
          iconColor: 'text-red-400'
        };
      case 'warning':
        return {
          gradient: 'from-orange-600 to-orange-800',
          button: 'bg-orange-600 hover:bg-orange-700',
          iconBg: 'bg-orange-500/20',
          iconColor: 'text-orange-400'
        };
      case 'info':
        return {
          gradient: 'from-blue-600 to-blue-800',
          button: 'bg-blue-600 hover:bg-blue-700',
          iconBg: 'bg-blue-500/20',
          iconColor: 'text-blue-400'
        };
      default:
        return {
          gradient: 'from-red-600 to-red-800',
          button: 'bg-red-600 hover:bg-red-700',
          iconBg: 'bg-red-500/20',
          iconColor: 'text-red-400'
        };
    }
  };

  const getDefaultIcon = () => {
    switch (type) {
      case 'danger':
        return <Trash2 size={32} />;
      case 'warning':
        return <AlertTriangle size={32} />;
      case 'info':
        return <CheckCircle size={32} />;
      default:
        return <AlertTriangle size={32} />;
    }
  };

  const styles = getTypeStyles();
  const displayIcon = icon || getDefaultIcon();

  return (
    <div className="fixed inset-0 bg-black/80 backdrop-blur-sm z-50 flex items-center justify-center p-4" dir="rtl">
      <div className="bg-gradient-to-br from-slate-900 via-blue-900 to-slate-900 rounded-2xl shadow-2xl w-full max-w-md border border-white/20 overflow-hidden">
        {/* Header */}
        <div className={`bg-gradient-to-r ${styles.gradient} p-6 text-white flex items-center justify-between`}>
          <div className="flex items-center gap-3">
            <div className={`w-12 h-12 ${styles.iconBg} rounded-xl flex items-center justify-center ${styles.iconColor}`}>
              {displayIcon}
            </div>
            <div>
              <h2 className="text-xl font-bold">{title}</h2>
            </div>
          </div>
          <button
            onClick={onClose}
            disabled={isLoading}
            className="p-2 hover:bg-white/20 rounded-lg transition-colors disabled:opacity-50 disabled:cursor-not-allowed"
          >
            <X size={24} />
          </button>
        </div>

        {/* Content */}
        <div className="p-6">
          <p className="text-white/90 text-right mb-6 leading-relaxed">{message}</p>

          {/* Actions */}
          <div className="flex gap-3 justify-end">
            <button
              onClick={onClose}
              disabled={isLoading}
              className="px-6 py-2 bg-white/10 text-white rounded-lg font-semibold hover:bg-white/20 transition-colors disabled:opacity-50 disabled:cursor-not-allowed"
            >
              {cancelText}
            </button>
            <button
              onClick={onConfirm}
              disabled={isLoading}
              className={`px-6 py-2 ${styles.button} text-white rounded-lg font-semibold transition-all disabled:opacity-50 disabled:cursor-not-allowed flex items-center gap-2`}
            >
              {isLoading ? (
                <>
                  <div className="w-4 h-4 border-2 border-white/30 border-t-white rounded-full animate-spin" />
                  <span>...</span>
                </>
              ) : (
                confirmText
              )}
            </button>
          </div>
        </div>
      </div>
    </div>
  );
};

export default ConfirmationModal;
