/**
 * Cases Store - Zustand (TypeScript)
 *   state   
 *
 *  CODE-003: Migrated to TypeScript
 */

import { create } from 'zustand';
import { persist } from 'zustand/middleware';
import { apiGet } from '../utils/api';
import { logger } from '../utils/logger';
import type {
  Case,
  CaseStats,
  CaseStatus,
  CaseAnalysis,
  AnalysisResults,
  GraphData,
  TimelineEvent
} from '../types';

// ===========================
// Types
// ===========================

interface IntelligenceData {
  summary?: string;
  key_points?: string[];
  recommendations?: string[];
  [key: string]: unknown;
}

interface CasesState {
  // State
  selectedCase: Case | null;
  selectedCaseId: string | null;
  cases: Case[];
  stats: CaseStats | null;
  casesLoading: boolean;
  casesError: string | null;
  lastFetched: number | null;
  currentAnalysis: CaseAnalysis | null;
  analysisLoading: boolean;
  analysisError: string | null;
  intelligenceData: IntelligenceData | null;
  intelligenceLoading: boolean;
  timelineData: TimelineEvent[] | null;
  graphData: GraphData | null;

  // Actions - Cases List
  setCases: (cases: Case[]) => void;
  fetchCases: (force?: boolean) => Promise<{ cases: Case[]; stats: CaseStats | null }>;
  invalidateCache: () => void;
  addCase: (newCase: Case) => void;
  updateCase: (caseId: string, updates: Partial<Case>) => void;
  removeCase: (caseId: string) => void;
  setCasesLoading: (loading: boolean) => void;
  setCasesError: (error: string | null) => void;

  // Actions - Current Case
  selectCase: (caseData: Case | null) => void;
  clearSelectedCase: () => void;
  updateSelectedCase: (updates: Partial<Case>) => void;

  // Actions - Analysis
  setAnalysis: (analysis: CaseAnalysis | null) => void;
  setAnalysisLoading: (loading: boolean) => void;
  setAnalysisError: (error: string | null) => void;
  updateAnalysis: <K extends keyof CaseAnalysis>(key: K, value: CaseAnalysis[K]) => void;

  // Actions - Intelligence
  setIntelligence: (data: IntelligenceData | null) => void;
  setIntelligenceLoading: (loading: boolean) => void;
  clearIntelligence: () => void;

  // Actions - Timeline & Graph
  setTimeline: (data: TimelineEvent[] | null) => void;
  setGraph: (data: GraphData | null) => void;

  // Selectors
  getCaseById: (id: string) => Case | undefined;
  getCasesByStatus: (status: CaseStatus) => Case[];
  countByStatus: () => { completed: number; analyzing: number; failed: number; total: number };
  getAnalysisResults: () => AnalysisResults | null;

  // Reset
  reset: () => void;
}

// Cache duration in milliseconds (30 seconds)
const CACHE_DURATION = 30 * 1000;

// ===========================
// Store
// ===========================

export const useCasesStore = create<CasesState>()(
  persist(
    (set, get) => ({
      // ===========================
      // State
      // ===========================
      selectedCase: null,
      selectedCaseId: null,
      cases: [],
      stats: null,
      casesLoading: false,
      casesError: null,
      lastFetched: null,
      currentAnalysis: null,
      analysisLoading: false,
      analysisError: null,
      intelligenceData: null,
      intelligenceLoading: false,
      timelineData: null,
      graphData: null,

      // ===========================
      // Actions - Cases List
      // ===========================

      setCases: (cases: Case[]) => set({ cases }),

      fetchCases: async (force = false) => {
        const { casesLoading, lastFetched, cases } = get();

        if (casesLoading) {
          return { cases, stats: get().stats };
        }

        if (!force && cases.length > 0 && lastFetched) {
          const cacheAge = Date.now() - lastFetched;
          if (cacheAge < CACHE_DURATION) {
            return { cases, stats: get().stats };
          }
        }

        set({ casesLoading: true, casesError: null });

        try {
          const data = await apiGet('/api/cases', { showErrorToast: false });
          const result = {
            cases: (data.cases || []) as Case[],
            stats: (data.stats || { total: 0, critical: 0, high: 0, medium: 0 }) as CaseStats
          };

          set({
            cases: result.cases,
            stats: result.stats,
            casesLoading: false,
            lastFetched: Date.now(),
            casesError: null
          });
          return result;
        } catch (error) {
          const errorMessage = error instanceof Error ? error.message : 'Failed to load cases';
          logger.error('Failed to fetch cases:', error);
          set({
            casesLoading: false,
            casesError: errorMessage
          });
          return { cases: [], stats: null };
        }
      },

      invalidateCache: () => set({ lastFetched: null }),

      addCase: (newCase: Case) => set((state) => ({
        cases: [newCase, ...state.cases]
      })),

      updateCase: (caseId: string, updates: Partial<Case>) => set((state) => ({
        cases: state.cases.map((c) =>
          c.id === caseId ? { ...c, ...updates } : c
        )
      })),

      removeCase: (caseId: string) => set((state) => ({
        cases: state.cases.filter((c) => c.id !== caseId),
        ...(state.selectedCaseId === caseId && {
          selectedCase: null,
          selectedCaseId: null,
          currentAnalysis: null
        })
      })),

      setCasesLoading: (loading: boolean) => set({ casesLoading: loading }),

      setCasesError: (error: string | null) => set({ casesError: error }),

      // ===========================
      // Actions - Current Case
      // ===========================

      selectCase: (caseData: Case | null) => set({
        selectedCase: caseData,
        selectedCaseId: caseData?.id || null
      }),

      clearSelectedCase: () => set({
        selectedCase: null,
        selectedCaseId: null,
        currentAnalysis: null,
        intelligenceData: null,
        timelineData: null,
        graphData: null
      }),

      updateSelectedCase: (updates: Partial<Case>) => set((state) => ({
        selectedCase: state.selectedCase
          ? { ...state.selectedCase, ...updates }
          : null
      })),

      // ===========================
      // Actions - Analysis
      // ===========================

      setAnalysis: (analysis: CaseAnalysis | null) => set({
        currentAnalysis: analysis,
        analysisError: null
      }),

      setAnalysisLoading: (loading: boolean) => set({ analysisLoading: loading }),

      setAnalysisError: (error: string | null) => set({ analysisError: error }),

      updateAnalysis: <K extends keyof CaseAnalysis>(key: K, value: CaseAnalysis[K]) => set((state) => ({
        currentAnalysis: state.currentAnalysis
          ? { ...state.currentAnalysis, [key]: value }
          : null
      })),

      // ===========================
      // Actions - Intelligence
      // ===========================

      setIntelligence: (data: IntelligenceData | null) => set({ intelligenceData: data }),

      setIntelligenceLoading: (loading: boolean) => set({ intelligenceLoading: loading }),

      clearIntelligence: () => set({ intelligenceData: null }),

      // ===========================
      // Actions - Timeline & Graph
      // ===========================

      setTimeline: (data: TimelineEvent[] | null) => set({ timelineData: data }),

      setGraph: (data: GraphData | null) => set({ graphData: data }),

      // ===========================
      // Selectors
      // ===========================

      getCaseById: (id: string): Case | undefined => {
        const { cases } = get();
        return cases.find((c) => c.id === id);
      },

      getCasesByStatus: (status: CaseStatus): Case[] => {
        const { cases } = get();
        return cases.filter((c) => c.status === status);
      },

      countByStatus: () => {
        const { cases } = get();
        return {
          completed: cases.filter((c) => c.status === 'completed').length,
          analyzing: cases.filter((c) => c.status === 'analyzing').length,
          failed: cases.filter((c) => c.status === 'failed').length,
          total: cases.length
        };
      },

      getAnalysisResults: (): AnalysisResults | null => {
        const { currentAnalysis } = get();
        if (!currentAnalysis) return null;

        return {
          contradictions: currentAnalysis.contradictions || [],
          claims: currentAnalysis.claims || [],
          entities: currentAnalysis.entities || [],
          insights: currentAnalysis.insights || [],
          roadmap: currentAnalysis.roadmap || [],
          strategies: currentAnalysis.strategies || []
        };
      },

      // ===========================
      // Reset
      // ===========================

      reset: () => set({
        selectedCase: null,
        selectedCaseId: null,
        cases: [],
        casesLoading: false,
        casesError: null,
        currentAnalysis: null,
        analysisLoading: false,
        analysisError: null,
        intelligenceData: null,
        intelligenceLoading: false,
        timelineData: null,
        graphData: null
      })
    }),
    {
      name: 'cases-storage',
      partialize: (state) => ({
        selectedCaseId: state.selectedCaseId,
      })
    }
  )
);
