/**
 * SmartOnboardingWizard -    
 *
 * 4   :
 * 1.   -   
 * 2.   -   
 * 3.     - 
 * 4.   -  
 */

import React, { useState } from 'react';
import {
  Scale,
  User,
  Settings,
  Sparkles,
  ArrowRight,
  ArrowLeft,
  Check,
  Briefcase,
  MessageSquare,
  Zap
} from 'lucide-react';
import { toast } from 'sonner';
import { apiPut } from '../utils/api';
import { useNavigate } from 'react-router-dom';

interface OnboardingData {
  displayName: string;
  role: string;
  yearsExperience: number;
  specializations: string[];
  responseLength: 'concise' | 'balanced' | 'detailed';
  includesCitations: boolean;
  proactiveSuggestions: boolean;
}

const STEPS = [
  { id: 'welcome', title: ' ', icon: Scale },
  { id: 'identity', title: ' ', icon: User },
  { id: 'preferences', title: ' ', icon: Settings },
  { id: 'taste', title: ' ', icon: Sparkles },
];

const ROLES = [
  { value: 'lawyer', label: '/ ', icon: '' },
  { value: 'judge', label: '/', icon: '' },
  { value: 'prosecutor', label: '/', icon: '' },
  { value: 'legal_advisor', label: '/ /', icon: '' },
  { value: 'intern', label: '', icon: '' },
  { value: 'student', label: '/', icon: '' },
];

const SPECIALIZATIONS = [
  '', ' ', ' ', '',
  ' ', '', '', '', '', ''
];

interface Props {
  isOpen: boolean;
  onClose: () => void;
  onComplete: () => void;
}

export default function SmartOnboardingWizard({ isOpen, onClose, onComplete }: Props) {
  const navigate = useNavigate();
  const [currentStep, setCurrentStep] = useState(0);
  const [isLoading, setIsLoading] = useState(false);

  const [data, setData] = useState<OnboardingData>({
    displayName: '',
    role: '',
    yearsExperience: 0,
    specializations: [],
    responseLength: 'balanced',
    includesCitations: true,
    proactiveSuggestions: true,
  });

  // Animation state
  const [isAnimating, setIsAnimating] = useState(false);

  const goNext = () => {
    if (currentStep < STEPS.length - 1) {
      setIsAnimating(true);
      setTimeout(() => {
        setCurrentStep(currentStep + 1);
        setIsAnimating(false);
      }, 200);
    }
  };

  const goBack = () => {
    if (currentStep > 0) {
      setIsAnimating(true);
      setTimeout(() => {
        setCurrentStep(currentStep - 1);
        setIsAnimating(false);
      }, 200);
    }
  };

  const toggleSpecialization = (spec: string) => {
    const current = data.specializations;
    if (current.includes(spec)) {
      setData({ ...data, specializations: current.filter(s => s !== spec) });
    } else if (current.length < 5) {
      setData({ ...data, specializations: [...current, spec] });
    }
  };

  const saveAndComplete = async () => {
    setIsLoading(true);
    try {
      await apiPut('/api/user-profile', {
        professional_identity: {
          display_name: data.displayName,
          role: ROLES.find(r => r.value === data.role)?.label || data.role,
          years_experience: data.yearsExperience,
          specializations: data.specializations,
        },
        work_preferences: {
          response_length: data.responseLength,
          include_citations: data.includesCitations,
          proactive_suggestions: data.proactiveSuggestions,
        },
      });

      toast.success('  !');
      onComplete();
    } catch (error) {
      console.error('Failed to save profile:', error);
      toast.error('  ');
    } finally {
      setIsLoading(false);
    }
  };

  if (!isOpen) return null;

  return (
    <div className="fixed inset-0 bg-black/70 backdrop-blur-sm flex items-center justify-center z-50 p-4" dir="rtl">
      <div className="bg-gradient-to-br from-slate-900 via-slate-800 to-slate-900 rounded-3xl max-w-2xl w-full border border-white/20 shadow-2xl overflow-hidden">

        {/* Progress bar */}
        <div className="h-1 bg-white/10">
          <div
            className="h-full bg-gradient-to-r from-blue-500 to-purple-500 transition-all duration-500"
            style={{ width: `${((currentStep + 1) / STEPS.length) * 100}%` }}
          />
        </div>

        {/* Header */}
        <div className="p-6 border-b border-white/10">
          <div className="flex items-center justify-between">
            <div className="flex items-center gap-4">
              {STEPS.map((step, idx) => (
                <div
                  key={step.id}
                  className={`flex items-center gap-2 ${
                    idx === currentStep
                      ? 'text-white'
                      : idx < currentStep
                      ? 'text-green-400'
                      : 'text-gray-500'
                  }`}
                >
                  <div className={`w-8 h-8 rounded-full flex items-center justify-center ${
                    idx === currentStep
                      ? 'bg-blue-600'
                      : idx < currentStep
                      ? 'bg-green-600'
                      : 'bg-white/10'
                  }`}>
                    {idx < currentStep ? (
                      <Check size={16} />
                    ) : (
                      <step.icon size={16} />
                    )}
                  </div>
                  {idx === currentStep && (
                    <span className="text-sm font-medium hidden sm:block">{step.title}</span>
                  )}
                </div>
              ))}
            </div>

            <button
              onClick={onClose}
              className="text-gray-400 hover:text-white transition-colors"
            >
              
            </button>
          </div>
        </div>

        {/* Content */}
        <div className={`p-8 min-h-[400px] transition-opacity duration-200 ${isAnimating ? 'opacity-0' : 'opacity-100'}`}>

          {/* Step 1: Welcome */}
          {currentStep === 0 && (
            <div className="text-center">
              <div className="w-24 h-24 mx-auto mb-6 bg-gradient-to-br from-blue-500 to-purple-600 rounded-full flex items-center justify-center">
                <Scale className="text-white" size={48} />
              </div>
              <h2 className="text-3xl font-bold text-white mb-4">  </h2>
              <p className="text-blue-200 text-lg mb-8 max-w-md mx-auto">
                     ,  ,   .
                            .
              </p>

              <div className="flex justify-center gap-4">
                <div className="flex items-center gap-2 text-green-400 bg-green-500/10 px-4 py-2 rounded-lg">
                  <Zap size={18} />
                  <span> </span>
                </div>
                <div className="flex items-center gap-2 text-purple-400 bg-purple-500/10 px-4 py-2 rounded-lg">
                  <MessageSquare size={18} />
                  <span> </span>
                </div>
                <div className="flex items-center gap-2 text-blue-400 bg-blue-500/10 px-4 py-2 rounded-lg">
                  <Briefcase size={18} />
                  <span></span>
                </div>
              </div>
            </div>
          )}

          {/* Step 2: Identity */}
          {currentStep === 1 && (
            <div>
              <h2 className="text-2xl font-bold text-white mb-2">    </h2>
              <p className="text-blue-200 mb-6">       </p>

              <div className="space-y-6">
                <div>
                  <label className="block text-blue-200 text-sm mb-2">  ?</label>
                  <input
                    type="text"
                    value={data.displayName}
                    onChange={(e) => setData({ ...data, displayName: e.target.value })}
                    placeholder='"  '
                    className="w-full px-4 py-3 bg-white/10 border border-white/20 rounded-xl text-white placeholder-gray-500 focus:border-blue-500 focus:outline-none"
                  />
                </div>

                <div>
                  <label className="block text-blue-200 text-sm mb-3">  ?</label>
                  <div className="grid grid-cols-3 gap-3">
                    {ROLES.map(role => (
                      <button
                        key={role.value}
                        onClick={() => setData({ ...data, role: role.value })}
                        className={`p-3 rounded-xl text-center transition-all ${
                          data.role === role.value
                            ? 'bg-blue-600 text-white border-2 border-blue-400'
                            : 'bg-white/10 text-gray-300 hover:bg-white/20 border-2 border-transparent'
                        }`}
                      >
                        <div className="text-2xl mb-1">{role.icon}</div>
                        <div className="text-sm">{role.label}</div>
                      </button>
                    ))}
                  </div>
                </div>

                <div>
                  <label className="block text-blue-200 text-sm mb-2"> </label>
                  <div className="flex items-center gap-4">
                    <input
                      type="range"
                      min="0"
                      max="40"
                      value={data.yearsExperience}
                      onChange={(e) => setData({ ...data, yearsExperience: parseInt(e.target.value) })}
                      className="flex-1"
                    />
                    <span className="text-white font-bold w-12 text-center">
                      {data.yearsExperience === 0 ? '' : `${data.yearsExperience}+`}
                    </span>
                  </div>
                </div>

                <div>
                  <label className="block text-blue-200 text-sm mb-3">  ( 5)</label>
                  <div className="flex flex-wrap gap-2">
                    {SPECIALIZATIONS.map(spec => (
                      <button
                        key={spec}
                        onClick={() => toggleSpecialization(spec)}
                        className={`px-3 py-1.5 rounded-full text-sm transition-all ${
                          data.specializations.includes(spec)
                            ? 'bg-purple-600 text-white'
                            : 'bg-white/10 text-gray-300 hover:bg-white/20'
                        }`}
                      >
                        {spec}
                      </button>
                    ))}
                  </div>
                </div>
              </div>
            </div>
          )}

          {/* Step 3: Preferences */}
          {currentStep === 2 && (
            <div>
              <h2 className="text-2xl font-bold text-white mb-2">   ?</h2>
              <p className="text-blue-200 mb-6">      </p>

              <div className="space-y-6">
                <div>
                  <label className="block text-blue-200 text-sm mb-3">  </label>
                  <div className="grid grid-cols-3 gap-3">
                    {[
                      { value: 'concise', label: ' ', desc: ' ', icon: '' },
                      { value: 'balanced', label: '', desc: ' +  ', icon: '' },
                      { value: 'detailed', label: '', desc: ' ', icon: '' },
                    ].map(opt => (
                      <button
                        key={opt.value}
                        onClick={() => setData({ ...data, responseLength: opt.value as any })}
                        className={`p-4 rounded-xl text-center transition-all ${
                          data.responseLength === opt.value
                            ? 'bg-blue-600 text-white border-2 border-blue-400'
                            : 'bg-white/10 text-gray-300 hover:bg-white/20 border-2 border-transparent'
                        }`}
                      >
                        <div className="text-2xl mb-2">{opt.icon}</div>
                        <div className="font-medium">{opt.label}</div>
                        <div className="text-xs opacity-70 mt-1">{opt.desc}</div>
                      </button>
                    ))}
                  </div>
                </div>

                <div className="space-y-3">
                  <div className="flex items-center justify-between p-4 bg-white/5 rounded-xl">
                    <div>
                      <div className="text-white font-medium">  </div>
                      <div className="text-gray-400 text-sm">  </div>
                    </div>
                    <button
                      onClick={() => setData({ ...data, includesCitations: !data.includesCitations })}
                      className={`w-12 h-6 rounded-full transition-colors ${
                        data.includesCitations ? 'bg-green-500' : 'bg-gray-600'
                      }`}
                    >
                      <div className={`w-5 h-5 bg-white rounded-full transition-transform ${
                        data.includesCitations ? 'translate-x-6' : 'translate-x-1'
                      }`} />
                    </button>
                  </div>

                  <div className="flex items-center justify-between p-4 bg-white/5 rounded-xl">
                    <div>
                      <div className="text-white font-medium"> </div>
                      <div className="text-gray-400 text-sm">   </div>
                    </div>
                    <button
                      onClick={() => setData({ ...data, proactiveSuggestions: !data.proactiveSuggestions })}
                      className={`w-12 h-6 rounded-full transition-colors ${
                        data.proactiveSuggestions ? 'bg-green-500' : 'bg-gray-600'
                      }`}
                    >
                      <div className={`w-5 h-5 bg-white rounded-full transition-transform ${
                        data.proactiveSuggestions ? 'translate-x-6' : 'translate-x-1'
                      }`} />
                    </button>
                  </div>
                </div>
              </div>
            </div>
          )}

          {/* Step 4: Taste */}
          {currentStep === 3 && (
            <div className="text-center">
              <div className="w-20 h-20 mx-auto mb-6 bg-gradient-to-br from-green-500 to-emerald-600 rounded-full flex items-center justify-center">
                <Sparkles className="text-white" size={40} />
              </div>
              <h2 className="text-2xl font-bold text-white mb-4">!  </h2>
              <p className="text-blue-200 mb-8 max-w-md mx-auto">
                {data.displayName ? ` ${data.displayName}! ` : ''}
                          .
                   ?
              </p>

              <div className="grid grid-cols-2 gap-4 max-w-md mx-auto">
                <button
                  onClick={() => {
                    saveAndComplete();
                    navigate('/cases');
                  }}
                  className="p-4 bg-gradient-to-br from-blue-500 to-purple-600 rounded-xl text-white hover:opacity-90 transition-opacity"
                >
                  <Briefcase size={24} className="mx-auto mb-2" />
                  <div className="font-medium">  </div>
                  <div className="text-xs opacity-70">  </div>
                </button>

                <button
                  onClick={() => {
                    saveAndComplete();
                    navigate('/profile');
                  }}
                  className="p-4 bg-white/10 border border-white/20 rounded-xl text-white hover:bg-white/20 transition-colors"
                >
                  <User size={24} className="mx-auto mb-2" />
                  <div className="font-medium"> </div>
                  <div className="text-xs opacity-70">  </div>
                </button>
              </div>
            </div>
          )}
        </div>

        {/* Footer */}
        <div className="p-6 border-t border-white/10 flex justify-between">
          <button
            onClick={goBack}
            disabled={currentStep === 0}
            className={`flex items-center gap-2 px-4 py-2 rounded-lg transition-colors ${
              currentStep === 0
                ? 'text-gray-500 cursor-not-allowed'
                : 'text-white hover:bg-white/10'
            }`}
          >
            <ArrowRight size={18} />
            
          </button>

          {currentStep < STEPS.length - 1 ? (
            <button
              onClick={goNext}
              className="flex items-center gap-2 px-6 py-2 bg-blue-600 text-white rounded-lg hover:bg-blue-700 transition-colors"
            >
              
              <ArrowLeft size={18} />
            </button>
          ) : (
            <button
              onClick={saveAndComplete}
              disabled={isLoading}
              className="flex items-center gap-2 px-6 py-2 bg-gradient-to-r from-green-500 to-emerald-600 text-white rounded-lg hover:opacity-90 transition-opacity disabled:opacity-50"
            >
              {isLoading ? (
                <span className="animate-spin"></span>
              ) : (
                <Check size={18} />
              )}
               
            </button>
          )}
        </div>
      </div>
    </div>
  );
}
